<template>
  <v-card class="bigBox">
    <!-- 表格 用户信息 -->
    <mrc_ess_table v-if="$route.name=='clientmanage'"
                   :addBtnList='golist'
                   :headers='headers'
                   :paginationOptions='options'
                   @turnPage='getlist'
                   :slotKey="['name']"
                   :isLoading='loading'
                   :items='desserts'>
      <template v-slot:top>

      </template>
      <template v-slot:item.name="{item}">
        <div>
          <span>{{INT(item.client_name)}}</span>
          <span class="n1">{{item.client_short_code?'('+item.client_short_code+')':''}}</span>
        </div>
      </template>

      <template v-slot:item.actions='{item}'>
        <menu-list :list='menulist'
                   @handleClick='handleClick($event,item)'>
          <template v-slot:showAction>
            <v-icon>mdi-dots-vertical</v-icon>
          </template>
        </menu-list>
      </template>
    </mrc_ess_table>
    <router-view></router-view>
  </v-card>
</template> 

<script>
import { getclientlist, edmitclientdetail } from '@/service/api/clientmanage_api.js'
export default {
  data () {
    return {
      loading: false,
      options: {
        pagesize: this.$store.state.pageSize,//条数
        cpage: this.$store.state.cpage,  //页数
        totalCount: null, //总条数
        totalpage: null, //总页数
      },
      dialog: false,
      desserts: []
    }
  },

  computed: {
    golist () {
      return [
        {
          name: this.$t('label_A_new_user'),
          page_name: 'addnewuser'
        },
        {
          name: this.$t('label_Add_from_existing_users'),
          page_name: 'addcopyuser'
        }]
    },
    menulist () {
      return [
        { name: this.$t('label_View') },
        { name: this.$t('label_Edit') },
        { name: this.$t('label_delete') }
      ]
    },
    headers () {
      return [
        { text: this.$t('label_The_customer_name'), align: 'start', sortable: false, value: 'name', },
        { text: '', value: 'actions', sortable: false, align: 'end', width: '60' },
      ]
    }
  },
  watch: {
    $route (val) {  //监听路由变化
      if (this.$route.name == 'clientmanage') {
        // console.log('更新list');
        this.getlist()
      }
    }
  },
  created () {
  },
  beforeDestroy () {
    this.$store.commit('setCpage', 1)
  },
  async mounted () {
    await this.getlist()
  },
  methods: {
    godetail (item) {  //详情页  
      this.$store.commit('set_uuid', item.role_uuid)
      this.$store.commit('set_detailuuid', item.uuid)
      this.$store.commit('set_subPage_name', item.client_name)
      this.$store.commit('set_iconstatus', true) //图标默认显示
      this.$router.push({
        name: 'clientdetail',
        params: {
          name: item.client_name,
          uuid: item.uuid
        }
      })
    },
    // 编辑
    editPrompt (item) { //三点进入
      this.$store.commit('set_uuid', item.role_uuid)
      this.$store.commit('set_detailuuid', item.uuid)
      // this.$store.commit('set_detailclientname', item.client_name) subPage_name
      this.$store.commit('set_subPage_name', item.client_name)
      this.$store.commit('set_iconstatus', false)  // 图标状态
      this.$router.push({
        name: 'clientdetail',
        params: {
          name: item.client_name,
          uuid: item.uuid
        }
      })
    },
    handleClick ({ index }, item) {
      switch (index) {
        case 0:
          this.godetail(item)
          break
        case 1:
          this.editPrompt(item)
          break
        case 2:
          this.handleDel(item)
          break
      }
    },
    // 删除
    handleDel (item) {
      this.$myConfirm({
        content: this.$t('message_Confirm_the_deletion')
      }).then(() => {
        edmitclientdetail({ uuid: item.uuid }).then(res => {
          if (this.desserts.length == 1 && this.options.cpage != 1) this.options.cpage--
          this.getlist()
          this.$store.commit('newsTips', this.$t('message_The_deletion_was_successful'))
        })
      }).catch(() => { })
    },
    // 获取客户列表 
    async getlist (obj) {
      this.loading = true
      if (obj) {
        const { cpage, pagesize } = obj
        this.options = obj
      }
      const res = await getclientlist({
        cpage: this.options.cpage,
        pagesize: this.options.pagesize,
      })
      // console.log(res);
      if (res.code === '200') {
        this.desserts = res.data
        this.options.totalCount = res.totalCount
        this.options.totalpage = res.totalpage
      }
      this.loading = false
    }
  },
}
</script>

<style scoped>
.n1 {
  font-size: 14px;
  margin-left: 15px;
  color: #ccc;
}
