// 客户管理
import http from '../httpConfig'

const table = "mrbase_sys_clients"


// 获取客户列表
export const getclientlist = (params = {}) => {
  params['~table~'] = table
  return new Promise((resolve, reject) => {
    http.post('/api/client/list-client/v1', params)
      .then(res => {
        resolve(res)
      })
      .catch(err => {
        reject(err)
      })
  })
}

